import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from "react-router-dom";

import { useAuth } from '../../context/AuthContext';
import styled from 'styled-components';
import {
  FormGroup,
  Label,
  Input,
  Button,
} from '../../styles/components/Modal.styles';
import { fetchWarhammerStatus } from '../../redux/slices/warhammerSlice';
const LoginContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: ${({ theme }) => theme.colors.background.base};
`;

const LoginCard = styled.div`
  background: ${({ theme }) => theme.colors.background.surface};
  padding: ${({ theme }) => theme.spacing.large};
  border-radius: ${({ theme }) => theme.borderRadius.default};
  width: 100%;
  max-width: 400px;
  box-shadow: ${({ theme }) => theme.shadows.medium};
`;

const LoginHeader = styled.h1`
  color: ${({ theme }) => theme.colors.text.primary};
  margin-bottom: ${({ theme }) => theme.spacing.large};
  text-align: center;
  font-size: ${({ theme }) => theme.typography.sizes.h1};
  font-weight: ${({ theme }) => theme.typography.weights.bold};
`;

const ErrorMessage = styled.div`
  color: ${({ theme }) => theme.colors.text.error};
  margin-bottom: ${({ theme }) => theme.spacing.medium};
  text-align: center;
  font-size: ${({ theme }) => theme.typography.sizes.small};
`;

// logged out message styled like an alert box
const LoggedOutMessage = styled.div`
  color: ${({ theme }) => theme.colors.text.primary};
  margin-bottom: ${({ theme }) => theme.spacing.medium};
  text-align: center;
  font-size: ${({ theme }) => theme.typography.sizes.medium};
  background-color: ${({ theme }) => theme.colors.background.base};
  border: 1px solid ${({ theme }) => theme.colors.background.warning};
  padding: ${({ theme }) => theme.spacing.medium};
  border-radius: ${({ theme }) => theme.borderRadius.default};
  box-shadow: ${({ theme }) => theme.shadows.medium};
  font-weight: ${({ theme }) => theme.typography.weights.bold};
`;

const Login = () => {
  const { login, logoutReason } = useAuth();
  const navigate = useNavigate();
  const [credentials, setCredentials] = useState({
    username: '',
    password: '',
  });
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const dispatch = useDispatch();
  const warhammerInfo = useSelector((state) => state?.warhammer?.data?.info);

  useEffect(() => {
    dispatch(fetchWarhammerStatus());
    console.log('logoutReason', logoutReason);
  }, [dispatch]);

  const handleChange = (field) => (e) => {
    setError(null);
    setCredentials(prev => ({
      ...prev,
      [field]: e.target.value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);
    setIsLoading(true);

    try {
      await login(credentials.username, credentials.password);
      navigate('/dashboard');
    } catch (err) {
      setError('Failed to login. Please check your credentials.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <LoginContainer>
      <LoginCard>
        <LoginHeader>{warhammerInfo?.name || 'WARHAMMER'}</LoginHeader>
        {logoutReason && (
          <LoggedOutMessage>
            {logoutReason}
          </LoggedOutMessage>
        )}
        <form onSubmit={handleSubmit}>
          <FormGroup>
            <Label>Username</Label>
            <Input
              type="text"
              value={credentials.username}
              onChange={handleChange('username')}
              placeholder="Enter username"
              disabled={isLoading}
            />
          </FormGroup>

          <FormGroup>
            <Label>Password</Label>
            <Input
              type="password"
              value={credentials.password}
              onChange={handleChange('password')}
              placeholder="Enter password"
              disabled={isLoading}
            />
          </FormGroup>

          {error && (
            <ErrorMessage>
              {error}
            </ErrorMessage>
          )}

          <Button
            type="submit"
            disabled={isLoading || !credentials.username || !credentials.password}
            style={{ width: '100%' }}
          >
            {isLoading ? 'Logging in...' : 'Login'}
          </Button>
        </form>
      </LoginCard>
    </LoginContainer>
  );
};

export default Login;
